.App {
  text-align: center;
  background-color: #F0F1F1;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

#greeting {
  padding-top: 3%;
  padding-bottom: 3%;
  width: 100%;
  background: linear-gradient(rgba(255,255,255,.6), rgba(255,255,255,.5)),
    url('./images/hr-form-background.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#search-results {
  margin: 2% auto;
  width: 50%;
  max-width: 700px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  font-family: 'Lato', sans-serif;
}

h1 {
  font-size: 1.8em;
}

h2 {
  font-size: 1.4em;
  font-weight: 800;
  font-style: italic;
}

h3 {
  font-size: 1.5em;
  margin-bottom: 16px;
}

h4 {
  font-size: 1.3em;
}

h5 {
  font-size: 1.2em;
  opacity: .9;
  margin: 8px 0;
}

button, a {
  cursor: pointer;
}

@media screen and (max-width: 1240px) {
  #search-results {
    width: 70%;
  }
}

@media screen and (max-width: 885px) {
  #search-results {
    width: 80%;
  }
}

@media screen and (max-width: 768px) {
  #search-results {
    width: 100%;
  }
}
